@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: "Roboto Condensed", sans-serif;
}

:root {
  --primary-white: #fff;
  --primary-orange: #f9ab00;
  --primary-dark: rgba(255, 255, 255, 0);
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
}

/*******NAVBAR********/

nav {
  z-index: 2;
}

.logo {
  margin-top: 1.2rem;
  width: 4.1rem;
  height: 4rem;
}

nav a.nav-link {
  /*font-weight: 500;*/
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav>li>a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  font-size: 1rem;
  border-top: 0.1875rem solid rgba(255, 255, 255, 0);
  border-top: 0.1875rem solid var(--primary-dark);
  color: #fff !important;
  color: var(--primary-white) !important;
}

.navbar-brand {
  max-width: 17.25rem;
  padding: 0 !important;
  height: 6rem;
}

.navbar-nav>li>a.active, .navbar-nav>li>a.active:focus {
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}

.navbar-nav>li>a:hover {
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}

/***************HEADER*****************/

.header-wraper {
  position: relative;
  background: url("https://images.pexels.com/photos/1074882/pexels-photo-1074882.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260") no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
}

.main-info {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  /*Center,Left,Right The Absolute*/
  position: absolute;
  top: 40%;
  left: 0%;
  right: 40%;
  /*CENTER H1*/
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  font-family: "Roboto Condensed", sans-serif;
}

.main-info h1 {
  font-size: 5rem;
  color: #f9ab00 !important;
  color: var(--primary-orange) !important;
  padding-right: 1.25rem;
  padding-left: 1.25rem;
}

@media (max-width:1500px) and (min-width:769px) {
  .header-wraper {
    position: relative;
    background: url("https://images.pexels.com/photos/1074882/pexels-photo-1074882.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260") no-repeat;
    background-size: cover;
    background-position: center;
    height: 200vh;
  }
  .main-info {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    /*Center,Left,Right The Absolute*/
    position: absolute;
    top: 50%;
    left: 0%;
    right: 40%;
    /*CENTER H1*/
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
    font-family: "Roboto Condensed", sans-serif;
  }
  .main-info h1 {
    font-size: 2.5rem;
    color: #f9ab00 !important;
    color: var(--primary-orange) !important;
    padding-right: 1.25rem;
    padding-left: 1.25rem;
  }
}

@media (max-width: 768px) {
  .main-info {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    /*Center,Left,Right The Absolute*/
    position: absolute;
    top: 0%;
    left: 0%;
    right: 0%;
    bottom: 0%;
    /*CENTER H1*/
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
    font-family: "Roboto Condensed", sans-serif;
  }
  .main-info h1 {
    font-size: 3rem !important;
  }
  .header-wraper {
    position: relative;
    background: url("https://images.pexels.com/photos/1074882/pexels-photo-1074882.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260") no-repeat;
    background-size: cover;
    background-position: right;
    height: 90vh;
  }
  .typed-text {
    font-size: 2.5rem !important;
    font-weight: 600;
    color: #fff;
    color: var(--primary-white);
  }
}

/**************TYPED TEXT**********************/

.typed-text {
  font-size: 3rem;
  font-weight: 600;
  color: #fff;
  color: var(--primary-white);
}

.mycv {
  font-size: 1.3rem;
  color: #fff !important;
  color: var(--primary-white) !important;
  background: #f9ab00;
  background: var(--primary-orange);
  text-transform: uppercase;
  border-radius: 5rem;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  margin-top: 2rem;
  z-index: 3;
}

.mycv:hover {
  background: #f33501;
  background: var(--primary-hover-red);
  font-size: 1.4rem;
  letter-spacing: 0.05rem;
  transition: 0.4s ease-in;
}

/********************PARTICLES***************/

.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}

@media (max-width: 768px) {
  .particles-canvas {
    display: none;
  }
}
